import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Select, Upload, Icon, Button } from 'antd';
// import { teachers } from 'dump/data';

// const handleInitialValue = initialValue => {
//   const initialValueDefault = {
//     code: '',
//     name: '',
//     birthdate: '',
//     academicRank: '',
//     degree: '',
//     title: '',
//     position: '',
//   };
//   return { ...initialValueDefault, ...initialValue };
// };

const { Option } = Select;

@Form.create({ name: 'formTeacher' })
class FormTrainingScoreExcel extends Component {
  state = {
    teacherId: true,
    // initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  // getTeacherById = code => {
  //   const teacherMap = teachers.filter(teacher => teacher.code === code)[0];
  //   const initialValue = handleInitialValue(teacherMap);
  //   this.setState({ initialValue });
  // };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    // const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Card title={teacherId ? 'Nhập điểm rèn luyện từ Excel' : 'Nhập điểm rèn luyện từ Excel'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item label="Chọn Sheet:">
              {getFieldDecorator('sheet', {
                initialValue: 'Sheet 1$',
              })(
                <Select defaultValue="aaa">
                  <Option value="aaa">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item label="Upload File">
              {getFieldDecorator('upload', {
                valuePropName: 'fileList',
                getValueFromEvent: this.normFile,
              })(
                <Upload name="logo" action="/upload.do" listType="picture">
                  <Button>
                    <Icon type="upload" /> Click to upload
                  </Button>
                </Upload>
              )}
            </Form.Item>

            <Form.Item label="Chọn cột mã sv">
              {getFieldDecorator('codeStudentCol', {
                initialValue: 'Mã sinh viên',
              })(
                <Select defaultValue="aaa">
                  <Option value="aaa">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item label="Chọn cột dữ liệu điểm">
              {getFieldDecorator('scoreDataCol', {
                initialValue: 'Điểm RL',
              })(
                <Select defaultValue="aaa">
                  <Option value="aaa">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item label="Chọn cột ghi chú">
              {getFieldDecorator('noteCol', {
                initialValue: 'Ghi chú',
              })(
                <Select defaultValue="aaa">
                  <Option value="aaa">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#training-score/add" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormTrainingScoreExcel {...props} />
  </Page>
));



